<template>
    <div>
        <el-container>
            <el-header>Header</el-header>
            <el-container>
                <el-aside width="200px" height="100%">
                    <el-menu
                        default-active="4"
                        class="el-menu-vertical-demo"
                        router>
                        <el-menu-item index="1" route="/admin_index">
                            <i class="el-icon-menu"></i>
                            <span slot="title">统计分析</span>
                        </el-menu-item>
                        <el-menu-item index="4" route="admin_category">
                            <i class="el-icon-menu"></i>
                            <span slot="title">分类管理</span>
                        </el-menu-item>
                        <el-menu-item index="5" route="admin_goods">
                            <i class="el-icon-menu"></i>
                            <span slot="title">商品管理</span>
                        </el-menu-item>
                        <el-menu-item index="6" route="admin_order">
                            <i class="el-icon-menu"></i>
                            <span slot="title">订单管理</span>
                        </el-menu-item>
                    </el-menu>
                </el-aside>
                <el-main>
                    <el-form :inline="true" :model="q" class="demo-form-inline">
                        <el-form-item label="昵称">
                            <el-input v-model="q.nickname" placeholder="昵称"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="query">查询</el-button>
                        </el-form-item>
                    </el-form>
                        <el-table
                        :data="categoryList"
                        style="width: 100%">
                        <el-table-column
                            prop="picUrl"
                            label="图片"
                            >
                        </el-table-column>
                        <el-table-column
                            prop="categoryName"
                            label="分类名称"
                            >
                        </el-table-column>
                        <el-table-column
                            prop="sort"
                            label="排序">
                        </el-table-column>
                        <el-table-column
                            prop="createTime"
                            label="创建时间"
                            >
                        </el-table-column>
                        </el-table>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>
<script>
export default {
  name: "AdminCategory",
  data() {
    return {
      categoryList: [],
      q: {

      }
    };
  },
  methods:{
      query(){
        var that = this;
        this.$axios({
            method: 'get',
            url: 'http://127.0.0.1:10001/category/list',
            data: that.q
        }).then(function(res){
            debugger
            if(res.data.code == 0){//成功
              that.categoryList = res.data.rows
            }else{
              
            }
        })
      }
  },
  created(){
    var user = localStorage.getItem("auser");
    if(user){
        this.user = JSON.parse(user);
    }else{
        this.$router.push("Login");
    }
    this.query()
  }
};
</script>

<style scoped>
.el-header, .el-footer {
  background-color: #B3C0D1;
  color: #333;
  line-height: 60px;
}
</style>


